import {UPDATE_CHART} from "../constants";

let initialState =
    {
        chart: [
            {time : '00:00', data : 0},
            {time : '03:00', data : 300},
            {time : '06:00', data : 600},
            {time : '09:00', data : 800},
            {time : '12:00', data : 1500},
            {time : '15:00', data : 2000},
            {time : '18:00', data : 2400},
            {time : '21:00', data : 100},
            {time : '24:00', data : undefined},
        ]
    };

let chartReducer = (state = initialState, action) => {
    switch (action.type) {
        case UPDATE_CHART :
            return {...state, chart: action.payload.chart};
        default :
            return state
    }
};


export default chartReducer;
