import React from 'react';
import {LineChart, Line, XAxis, YAxis, Label, ResponsiveContainer} from 'recharts';
import Title from './Title';
import {connect} from "react-redux";
// import updateChart from "../actions/updateChartAction";

// Generate Sales Data
function createData(time, amount) {
    return {time, amount};
}


class Chart extends React.Component {
    constructor(props) {
        super(props);
        let random = () => {
            return Math.floor(Math.random() * (2000 - 100) + 100);
        };

        this.secondData = () => {
            return ([
                {time: '00:00', data: random()},
                {time: '03:00', data: random()},
                {time: '06:00', data: random()},
                {time: '09:00', data: random()},
                {time: '12:00', data: random()},
                {time: '15:00', data: random()},
                {time: '18:00', data: random()},
                {time: '21:00', data: random()},
                {time: '24:00', data: undefined}])
        };

        this.state = {
            data: this.props.chart.map((item => createData(item.time, item.data)))
        }
    }

    componentDidMount() {
        this.setTimer();
    }

    componentWillUnmount() {
        clearInterval(this.timerID);
    }

    setTimer() {
        this.timerID = setInterval(
            () => {
                this.setState({
                    data: this.secondData().map((item => createData(item.time, item.data)))
                })
            },
            5000
        );
    }

    render() {
        return (
            <React.Fragment>
                <Title>Today</Title>
                <ResponsiveContainer>
                    <LineChart
                        data={this.state.data}
                        margin={{
                            top: 16,
                            right: 16,
                            bottom: 0,
                            left: 24,
                        }}
                    >
                        <XAxis dataKey="time"/>
                        <YAxis>
                            <Label angle={270} position="left" style={{textAnchor: 'middle'}}>
                                Sales ($)
                            </Label>
                        </YAxis>
                        <Line type="monotone" dataKey="amount" stroke="#556CD6" dot={false}/>
                    </LineChart>
                </ResponsiveContainer>
            </React.Fragment>
        );
    }
}


function initMapStateToProps(state) {
    console.log(state);
    return {
        chart: state.chart.chart
    }
}

export default connect(initMapStateToProps)(Chart);
